import {
  Container,
  Grid,
  Typography,
  Dialog,
  DialogTitle,
  DialogContentText,
  DialogContent,
  Theme,
  useTheme,
  Box,
} from '@mui/material';
import React, { useState, ComponentProps } from 'react';
import BenefitCard from 'components/Cards/BenefitCard';
import FadeUp from 'components/Animated/FadeUp';
import { useBooleanState } from '@lubycon/react';
import { mentoringIntroPageLogger } from 'src/utils/loggers';

interface MentoringBenefit {
  title: string;
  summary: string;
  image: string;
  description: string;
}
const benefits: MentoringBenefit[] = [
  {
    title: 'Lean \n  ',
    summary:
      'Lean Thinking            .',
    image: 'https://assets.lubycon.io/illusts/chart.png',
    description: `Lean Thinking            .\n\n                ,    .          MVP             .\n\n  12  Lean          !`,
  },
  {
    title: '  \n  ',
    summary: '  , ,        .',
    image: 'https://assets.lubycon.io/illusts/briefcase.png',
    description: `       ,     !\n\n     Chapter             , Guild   DevOps  ,            !`,
  },
  {
    title: '   \n ',
    summary: '         .',
    image: 'https://assets.lubycon.io/illusts/networking.png',
    description: `          .        ,              .\n\n     ,  ,             .       .\n\n         ,     !`,
  },
  {
    title: '  \n  ',
    summary: '            ?',
    image: 'https://assets.lubycon.io/illusts/database.png',
    description: `       DevOps   ,           .\n\n     DevOps    DevOps        , CI/CD   .      SaaS   .\n\n          ,                ,  DevOps        !`,
  },
];

const BenefitSection = () => {
  const { spacing } = useTheme<Theme>();
  const [isOpenModal, openModal, closeModal] = useBooleanState();
  const [selectedBenefit, selectBenefit] = useState<MentoringBenefit | null>(null);

  return (
    <Box component="section" sx={{ padding: { md: `${spacing(34)} 0`, xs: `${spacing(10)} 0` }, zIndex: 1 }}>
      <Container>
        <Typography component="h2" sx={{ fontWeight: 'bold', fontSize: { md: 56, xs: 24 } }}>
            
        </Typography>
        <Typography sx={{ marginBottom: { md: '82px', xs: '40px' } }}>
                    ,    
                     .
          <br />
          <br />
                   !
        </Typography>
        <Grid container spacing={2}>
          {benefits.map(({ title, image, description, summary }, index) => (
            <Grid item xs={12} sm={6} md={3} key={index}>
              <FadeUp>
                <BenefitCard
                  key={index}
                  title={title}
                  description={summary}
                  image={image}
                  onClick={() => {
                    mentoringIntroPageLogger.click('benefit_card', { title });
                    openModal();
                    selectBenefit({ title, image, description, summary });
                  }}
                />
              </FadeUp>
            </Grid>
          ))}
        </Grid>
      </Container>
      <BenefitDialog
        open={isOpenModal}
        onClose={() => {
          closeModal();
          selectBenefit(null);
        }}
        benefit={selectedBenefit}
      />
    </Box>
  );
};

interface Props extends ComponentProps<typeof Dialog> {
  benefit: MentoringBenefit | null;
}
const BenefitDialog = ({ benefit, ...rest }: Props) => {
  return (
    <Dialog {...rest}>
      <Box
        component="img"
        src={benefit?.image}
        alt=""
        sx={{ width: 120, paddingTop: 5, paddingLeft: 2, margin: '0 auto' }}
      />
      <DialogTitle sx={{ textAlign: 'center', fontWeight: 'bold' }}>{benefit?.title}</DialogTitle>
      <DialogContent>
        <DialogContentText
          sx={{ whiteSpace: 'pre-line', color: 'common.black', marginBottom: 5 }}
          dangerouslySetInnerHTML={{ __html: benefit?.description ?? '' }}
        />
      </DialogContent>
    </Dialog>
  );
};

export default BenefitSection;
