import { Container, Grid, Typography, Theme, Box, useTheme } from '@mui/material';
import React from 'react';
import { useAllMembers } from 'src/hooks/useAllMembers';
import Emoji from 'components/Utils/Emoji';
import FadeUp from 'components/Animated/FadeUp';
import MemberCard from 'components/Cards/MemberCard';
import RecruitMentorCircle from './RecruitMentorCircle';
import sandboxData from 'data/sandbox.json';

const { needRecruitMentors, mentorsThisRound } = sandboxData;

const MentorSection = () => {
  const { spacing } = useTheme<Theme>();
  const members = useAllMembers();
  const mentors = members.filter(member => mentorsThisRound.includes(member.id));

  return (
    <Box
      component="section"
      sx={{
        padding: {
          md: `${spacing(15)} 0`,
          xs: `${spacing(10)} 0`,
        },
      }}
    >
      <Container>
        <Typography
          variant="h4"
          component="h2"
          sx={{
            fontWeight: 'bold',
            textAlign: 'center',
            marginBottom: {
              md: 2,
              xs: 1,
            },
            fontSize: {
              md: 'h4.fontSize',
              xs: 20,
            },
          }}
        >
             
        </Typography>
        <Typography
          sx={{
            marginBottom: {
              md: 10,
              xs: 4,
            },
            textAlign: 'center',
            fontSize: {
              md: 18,
              xs: 16,
            },
          }}
        >
            1   12    . <Emoji emoji="" name="hi" />
          <br />
                     , <br />
                  ,      
          .
        </Typography>
        <Grid container>
          {mentors.map(mentor => (
            <Grid
              item
              key={mentor.id}
              xs={6}
              sm={4}
              md="auto"
              sx={{
                width: {
                  md: '25%',
                  xs: '50%',
                },
              }}
            >
              <FadeUp>
                <MemberCard member={mentor} backgroundColor="#FCD1CA" />
              </FadeUp>
            </Grid>
          ))}
          {needRecruitMentors ? (
            <Grid
              item
              xs={6}
              sm={4}
              md="auto"
              sx={{
                width: {
                  md: '25%',
                  xs: '50%',
                },
              }}
            >
              <FadeUp>
                <RecruitMentorCircle />
              </FadeUp>
            </Grid>
          ) : null}
        </Grid>
      </Container>
    </Box>
  );
};

export default MentorSection;
